/**
 * <mark>html
 * @param keywords  
 * @param content   
 * @returns {{__html: string}}    
 * Author: Wei Liu
 * Date: 2021/07/14
 */
export default (keywords=[], content="") => {
    keywords.forEach(item => {
        content = content.replace(item, `<mark>${item}</mark>`)
    })
    return {__html: content};
}